<template>
    <div class="back-top c-center" @click="pageScroll">
        <i class="icon iconfont icon-top"></i>
    </div>
</template>

<script>
    export default {
        name: 'backtop',
        props: ['left'],
        data() {
            return {};
        },
        methods: {
    
            /**
             * 左上角返回按钮，配置了left的值，则跳转到left，否则返回
             */
            leftClick() {
                if (this.left) {
                    location.href = this.left;
                    return;
                }
                history.back();
            },
    
            /**
             * 右上角按钮，会向父元素触发 rightClick 事件
             */
    
            pageScroll() {
                window.scroll(0, 0);
            }
    
    
        }
    };
</script>

<style lang="less" scoped>
    .back-top {
        background: rgba(0, 0, 0, 0.8);
        width: 40px;
        height: 40px;
        line-height: 40px;
        border-radius: 40px;
        position: fixed;
        right: 15px;
        bottom: 35px;
        color: #fff;
        i {
            font-size: 40px;
        }
    }
</style>


